import axios from "axios";

const api = {
  async getMovies() {
    const upcomingMovies = await axios.get(
      "https://yts-proxy.nomadcoders1.now.sh/list_upcoming.json",
      {
        params: {
          sort_by: "date_added",
        },
      }
    );

    // const popularMovies = await axios.get(
    //   "https://yts-proxy.nomadcoders1.now.sh/list_movies.json",
    //   {
    //     params: {
    //       sort_by: "rating",
    //     },
    //   }
    // );

    return {
      upcomingMovies: upcomingMovies.data.data,
      popularMovies: upcomingMovies.data.data,
    };
  },

  async getTv() {
    const upcomingTv = await axios.get(
      "https://yts-proxy.nomadcoders1.now.sh/list_upcoming.json",
      {
        params: {
          sort_by: "date_added",
        },
      }
    );

    const popularTv = await axios.get(
      "https://yts-proxy.nomadcoders1.now.sh/list_movies.json",
      {
        params: {
          sort_by: "rating",
        },
      }
    );
    return {
      upcomingTv: upcomingTv.data.data,
      popularTv: popularTv.data.data,
    };
  },
  async getDetail(id) {
    const {
      data: { data },
    } = await axios.get(
      "https://yts-proxy.nomadcoders1.now.sh/movie_details.json",
      {
        params: {
          movie_id: id,
        },
      }
    );
    return data;
  },

  async search(keyword) {
    const {
      data: { data },
    } = await axios.get(
      "https://yts-proxy.nomadcoders1.now.sh/list_movies.json",
      {
        params: {
          query_term: keyword,
          limit: 50,
        },
      }
    );
    return data;
  },
};

export default api;
